<template>
    <el-container class="out-container">
        <el-header class="site-header">
            <h1 class="logo">学生管理系统</h1>
            <div class="personal">
                <span>{{loginUser?loginUser.loginId:""}}</span>
                <el-button
                    @click="handleLoginOut"
                    type="danger"
                    size="mini"
                    style="margin-left:1em;"
                >注销</el-button>
            </div>
        </el-header>
        <el-container class="site-bottom">
            <el-aside
                class="site-aside"
                width="200px"
            >
                <el-menu
                    class="site-menu"
                    background-color="#545c64"
                    text-color="#fff"
                    active-text-color="#ffd04b"
                >
                    <el-menu-item index="2">
                        <i class="el-icon-document"></i>
                        <router-link :to="{name:'stulist'}">学生列表</router-link>
                    </el-menu-item>
                    <el-menu-item index="3">
                        <i class="el-icon-circle-plus-outline"></i>
                        <router-link :to="{name:'addstu'}">添加学生</router-link>
                    </el-menu-item>
                </el-menu>
            </el-aside>
            <el-main>
                <transition mode="out-in">
                    <router-view></router-view>
                </transition>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
export default {
    created() {
        if (!this.loginUser) {
            this.$router.replace({
                name: "login"
            });
        }
    },
    computed: {
        loginUser() {
            return this.$store.state.loginUser.data;
        }
    },
    methods: {
        handleLoginOut() {
            this.$store.dispatch("loginUser/loginOut");
            this.$message({
                message: "请重新登录",
                type: "error",
                duration: 1000,
                onClose: () => {
                    this.$router.replace({
                        name: "login"
                    });
                }
            });
        }
    }
};
</script>

